const LISTEN_RATE = 60;
const PERSPECTIVE_FACTOR = 1 / 1000;

export class PerspectiveService {
    private static instance: PerspectiveService;
    private centerX: number;
    private centerY: number;
    private counter: number;
    private wrapperElement: HTMLElement;
    private recipient: HTMLElement;

    private constructor() {
        window.addEventListener('resize', this.setCenter);
        window.addEventListener('mousemove', this.updatePerspective);

    }

    public static getInstance(): PerspectiveService {
        if (!this.instance) {
            this.instance = new PerspectiveService();
        }

        return this.instance;
    }

    public init = () => {
        this.centerX = 0;
        this.centerY = 0
        this.counter = 1;
        this.setCenter();
    }

    private setCenter(): void {
        const perspectiveWrapper = document.getElementById('perspectiveWrapper');
        const { clientWidth, clientHeight } = perspectiveWrapper;

        this.centerX = clientWidth / 2;
        this.centerY = clientHeight / 2;
    }

    private updatePerspective(event: MouseEvent): void {
        const isTimeToUpdate = LISTEN_RATE % this.counter === 0;

        if (!isTimeToUpdate) {
            this.counter = 1 + (this.counter || 0);
            return;
        }

        const { clientX, clientY } = event;
        const xAngle = (clientX - this.centerX) * PERSPECTIVE_FACTOR;
        const yAngle = (clientY - this.centerY) * PERSPECTIVE_FACTOR;
        const perspectiveRecipient = document.getElementById('perspectiveRecipient');
        perspectiveRecipient.style.transform = `rotateX(${xAngle}deg) rotateY(${yAngle}deg)`;
        this.counter = 1;
    }
}